#home {
  flex-direction: row;
  column-gap: 40px;
  padding: 40px 24px;
  display: flex;
}

#home > * {
  flex-shrink: 0;
}

#main_section {
  flex-shrink: 0;
  width: 996px;
}

#main_section h1 {
  background-color: #d3d3d3;
  margin: 0 0 40px;
}

#main_section .books_row_container {
  background-color: #d3d3d3;
  flex-direction: column;
  margin-bottom: 40px;
  display: flex;
}

#main_section .books_row_container button {
  all: unset;
  color: #111;
  letter-spacing: -.14px;
  text-transform: uppercase;
  cursor: pointer;
  border: 2px solid #4f2ee8;
  border-radius: 40px;
  align-self: flex-end;
  width: -moz-fit-content;
  width: fit-content;
  padding: 14px 28px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  display: block;
}

#main_section .books_row_container .books_row_title {
  color: #000;
  letter-spacing: .42px;
  text-transform: uppercase;
  margin: 0 0 18px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  margin-block-start: 0 !important;
}

#main_section .books_row_container .books_row {
  flex-direction: row;
  gap: 1rem;
  margin: 0 0 32px;
  padding: 0;
  list-style-type: none;
  display: flex;
}

#main_section .books_row_container .books_row li {
  background-color: #90ee90;
  border: 1px solid #000;
  width: 180px;
  height: 306px;
}

#sidebar {
  background-color: #add8e6;
  flex-direction: column;
  width: 356px;
  display: flex;
}

#sidebar ul {
  padding: 0;
  list-style: none;
}

#sidebar #menu-categories .categories-title {
  color: #4f2ee8;
  letter-spacing: -.36px;
  text-transform: uppercase;
  font-family: DM Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
}

#sidebar #menu-categories h3, #sidebar #menu-categories li {
  margin-bottom: 2rem;
}

#sidebar #ukraine-support {
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  width: 356px;
  height: 474px;
  padding: 26px 40px;
  box-shadow: 0 10px 10px #f5f5f71a;
}

#sidebar #ukraine-support .support-ukraine-title {
  margin-bottom: 40px;
}

#sidebar #ukraine-support .support-ukraine-list li {
  margin-bottom: 20px;
}

#SL-container {
  justify-content: center;
  margin: 40px 20px;
  display: flex;
}

#SL-container h1, #SL-container h2, #SL-container h3, #SL-container p {
  margin-block: 0;
}

#SL-support {
  display: none;
}

#SL-flex {
  flex-direction: column;
  align-items: center;
  display: flex;
}

#SL-title {
  color: #111;
  letter-spacing: -1.28px;
  align-self: flex-start;
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
  line-height: 38px;
}

#SL-title span {
  color: var(--purple-accent-color);
}

#SL-placeholder {
  flex-direction: column;
  gap: 14px;
  width: 265px;
  margin-top: 120px;
  display: flex;
}

#SL-placeholder p {
  color: #0009;
  text-align: center;
  letter-spacing: -.28px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

#SL-placeholder img {
  width: 265px;
  height: 198px;
}

#SL-list {
  flex-direction: column;
  gap: 20px;
  padding: 0;
  list-style: none;
  display: flex;
}

#SL-list li.SL-book {
  background: #fff;
  border: 2px solid #4f2ee866;
  border-radius: 16px;
  flex-direction: row;
  width: 335px;
  height: 170px;
  padding: 14px;
  display: flex;
  position: relative;
}

#SL-list li.SL-book img.SL-book-cover {
  width: 100px;
  height: 142px;
  margin-right: 14px;
}

#SL-list li.SL-book button.SL-remove {
  background-color: #4f2ee8;
  border: none;
  border-radius: 100000px;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 28px;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

#SL-list li.SL-book button.SL-remove svg {
  fill: #4f2ee8;
  width: 16px;
  height: 16px;
}

#SL-list li.SL-book .SL-d1 {
  flex-direction: column;
  width: 193px;
  display: flex;
}

#SL-list li.SL-book .SL-d1 h3.SL-book-title {
  color: #111;
  letter-spacing: -.64px;
  text-transform: uppercase;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

#SL-list li.SL-book .SL-d1 p.SL-book-category {
  color: #b4afaf;
  letter-spacing: .36px;
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

#SL-list li.SL-book .SL-d1 p.SL-book-description {
  text-overflow: ellipsis;
  white-space: pre-wrap;
  letter-spacing: -.56px;
  height: 70px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  overflow: hidden;
}

#SL-list li.SL-book .SL-d1 .SL-d2 {
  flex-direction: row;
  justify-content: space-between;
  align-items: end;
  margin-top: auto;
  display: flex;
}

#SL-list li.SL-book .SL-d1 .SL-d2 p.SL-book-author {
  color: #b4afaf;
  letter-spacing: -.48px;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
}

#SL-list li.SL-book .SL-d1 .SL-d2 .SL-d3 {
  align-items: center;
  gap: 8px;
  display: flex;
}

#SL-list li.SL-book .SL-d1 .SL-d2 .SL-d3 :first-child img {
  width: 32px;
  height: 11px;
}

#SL-list li.SL-book .SL-d1 .SL-d2 .SL-d3 :last-child img {
  width: 16px;
  height: 16px;
}

#SL-pages {
  gap: 14px;
  display: flex;
}

#SL-pages .SL-ring {
  color: #000;
  border-color: #000;
  border-radius: 99999px;
  width: 38px;
  height: 38px;
  font-size: 18px;
  font-weight: 700;
  line-height: 22px;
}

#SL-pages .SL-pages-box {
  gap: 8px;
  display: flex;
}

#SL-pages #SL-pages-first, #SL-pages #SL-pages-previous {
  color: #b4afaf;
  background: #111;
}

#SL-pages #SL-pages-actual {
  color: #fff;
  background: #111;
}

#SL-pages #SL-pages-next, #SL-pages #SL-pages-last {
  background: #eac645;
}

@media screen and (min-width: 767px) {
  #SL-container {
    margin: 40px 32px;
  }

  #SL-title {
    letter-spacing: -1.92px;
    font-size: 48px;
    line-height: 52px;
  }

  #SL-placeholder {
    gap: 20px;
    width: 349px;
    margin-top: 140px;
  }

  #SL-placeholder p {
    letter-spacing: -.36px;
    font-size: 18px;
    line-height: 24px;
  }

  #SL-placeholder img {
    width: 322px;
    height: 241px;
  }

  #SL-list li.SL-book {
    width: 704px;
    height: 213px;
    padding: 24px;
  }

  #SL-list li.SL-book img.SL-book-cover {
    width: 116px;
    height: 165px;
    margin-right: 24px;
  }

  #SL-list li.SL-book button.SL-remove {
    width: 34px;
    height: 34px;
    top: 24px;
    right: 24px;
  }

  #SL-list li.SL-book button.SL-remove svg {
    width: 18px;
    height: 18px;
  }

  #SL-list li.SL-book .SL-d1 {
    width: 508px;
  }

  #SL-list li.SL-book .SL-d1 p.SL-book-category {
    margin-bottom: 14px;
  }

  #SL-list li.SL-book .SL-d1 p.SL-book-description {
    height: 60px;
  }

  #SL-list li.SL-book .SL-d1 .SL-d2 .SL-d3 {
    gap: 16px;
  }

  #SL-list li.SL-book .SL-d1 .SL-d2 .SL-d3 :first-child img {
    width: 48px;
    height: 15px;
  }

  #SL-list li.SL-book .SL-d1 .SL-d2 .SL-d3 :last-child img {
    width: 28px;
    height: 27px;
  }

  #SL-pages {
    gap: 24px;
  }

  #SL-pages .SL-ring {
    width: 44px;
    height: 44px;
  }
}

@media screen and (min-width: 1200px) {
  #SL-container {
    justify-content: left;
    gap: 40px;
    margin: 40px 24px;
  }

  #SL-support {
    display: flex;
  }

  #SL-list li.SL-book {
    width: 996px;
  }

  #SL-list li.SL-book .SL-d1 {
    width: 808px;
  }
}

