  overflow: hidden;
}

.book-cover {
  z-index: 1;
  border-radius: 8px;
  width: 335px;
  height: 485px;
}

.book-cover-book-title {
  color: var(--black-text-color);
  letter-spacing: -.64px;
  text-transform: uppercase;
  margin-top: 14px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
